<template>
  <div>
    <form>

      <v-row>
        <v-col cols="12" sm="3">
          <p>Select platform</p>
          <v-select
            v-model="storedValues.platform"
            :items="platforms"
            label="Platform"
            required

          ></v-select>
    
        </v-col>

        <v-col cols="12" sm="3">
          <p>Select team</p>
          <v-select
            v-model="storedValues.team"
            :items="teams"
            label="Team"
            required

          ></v-select>
    
        </v-col>

        <v-col cols="12" sm="3">
          <p>Select automation tag value</p>
          <v-select
            v-model="storedValues.auto"
            :items="autoTagValues"
            label="Automation value"
            value="manual"
            required

          ></v-select>
    
        </v-col>

      </v-row>

      <v-btn class="mr-4" @click="submit" v-on:submit.prevent="onSubmit" method="POST">Get</v-btn>
    </form>

    <div v-if="isTextAreaVisible">
      <v-checkbox
        label="Prepend with C"
        v-model="prependC"
      ></v-checkbox>
      <v-textarea 
        :no-resize="noResize"
        :auto-grow="autoGrow"
        v-model="testCases"
      >
      </v-textarea>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'TestCaseFilter',
    data () {
      return {
        prependC: false,
        noResize: true,
        autoGrow: true,
        storedValues: {
          auto: 'automated',
          team: 'testlio',
          platform: 'all (OR)'
        },
        teams: ['internal', 'testlio'],
        platforms: ['android', 'ios', 'all (AND)', 'all (OR)'],
        autoTagValues: ['manual', 'planned', 'automated'],
        testCases: "",
        reservedResponse: [],
        isTextAreaVisible: false
        
      }
    },
    watch: {
      testCases: {
        handler: function(newValue) {
          this.isTextAreaVisible = newValue !== "";
        }
      },
      prependC: {
        handler: function(newValue) {
          var tempArr = []
          if (newValue === true) {
            tempArr = this.reservedResponse.map(function(val) {
              return 'C' + val;
            });
            this.testCases = tempArr.join('\n');
          } else {
            this.testCases = this.reservedResponse.join('\n')
          }
        }
      }
    },
    mounted() {

    },
    methods: {
      submit() {
        axios.post('/api/test_cases/', {
          auto_value: this.storedValues.auto,
          team: this.storedValues.team,
          platform: this.storedValues.platform
        })
        .then(resp => {
          this.reservedResponse = resp.data;
          this.testCases = this.reservedResponse.join('\n');
        })
      },
   
  }
}
</script>