<template>
  <v-data-table
    :headers="headers"
    :items="devices"
    :items-per-page="50"
    class="elevation-1"
  ></v-data-table>
</template>

<script>
    import axios from 'axios'

  export default {
    name: 'DeviceTable',
    data () {
      return {
        headers: [
          {
            text: 'Device name',
            align: 'left',
            sortable: false,
            value: 'name',
          },
          { text: 'OS', value: 'os' },
          { text: 'Version', value: 'os_version' },
  
        ],
        devices: [],
        
      }
    },
    mounted() {
        axios.get('/api/free_devices/')
            .then(resp => this.devices = resp.data)
    }
  }
</script>