import Vue from 'vue'
import Router from 'vue-router'
import TestdroidContainer from './components/TestdroidContainer.vue'
import TestrailContainer from './components/TestrailContainer.vue'
import TestlioContainer from './components/TestlioContainer.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/testdroid',
      name: 'testdroid',
      component: TestdroidContainer, 
    },
    {
      path: '/testrail',
      name: 'testrail',
      component: TestrailContainer, 
    },
    {
      path: '/testlio',
      name: 'testlio',
      component: TestlioContainer, 
    },

  ]
})
